import axios from "axios";

const http = axios.create({
  //TODO: Adjust this to work on .env
  baseURL: "http://localhost:8080",
  headers: {
    "Content-type": "application/json"
  }
});

class ZauiDataService {
    ping() {
      //pings the status of Zaui api, will return "pong" when its okay
        return http.post("/zaui/zaui-ping");
    }
    getBookingByDay(date){
        console.log(date);

        //TODO: Adjust this so it takes in the date we're looking to grab, probably only useful after we know what were building
        var testDate = new Date('Dec 1, 2023');
        //TODO: Create a way to read in any date and turn it into the string expected by Zaui (YYYY-MM-DD)
        testDate = testDate.toISOString().slice(0, 10);
        return http.get(`/zaui/get-booking-by-day?date=${testDate}`);

        //date = date.toISOString().slice(0, 10);
        // return http.get(`/zaui/get-booking-by-day?date=${date}`);
      
    }
}

export default new ZauiDataService();